
:root {
  --border-width: 6px;
}

body {
  margin: 0 auto;
  max-width: 100%;
  font-family: sans-serif;
  padding: 5px;
  color: #fff;
}

.arrow-down:after {
  border-top: 10px solid #ddd;
  border-left: 10px solid transparent; 
  border-right: 10px solid transparent;
  bottom: 0px;
  content: "";
  position: absolute;
  top: 100%;
  left: 55%;
  margin-left: -21px;
  width: 0; 
  height: 0;
}

/* .app {
  text-align: center;
} */

.row {
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-auto-rows: 200px;
  grid-gap: 50px;
  margin-bottom: 20px;
}

@media screen and (max-width: 992px) {

  .row {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-auto-rows: auto;
    grid-gap: 0px;
    margin-bottom: 0px;
    text-align: center;
  }

  .row h1 {
    margin-top: 45px;
    margin-left: -14px;
  }

  .row h2 {
    margin-top: 5px;
  }

  .row .intro {
    padding: 10px 45px 20px 45px
  }

  .paste-demo-gif{
    width: 70%;
  }

  .row .paste-demo-gif img{
    width: 70%;
  }

  .editor {
    margin: auto !important;
  }
  
}

.colour-panel {
  background-color: #b5c6c2;
  padding: 10px;
}


.intro {
  display: inline-block;
  color: #333;
  font-family: serif;
  font-size: 0.81rem;
  padding: 24px 30px 0px 0px;
  text-align: justify;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

.paste-demo-gif {
  margin-top: 6px;
  margin-bottom: 8px;
  background: white;
  -webkit-box-shadow: 0px 6px 15px 0px grey;
          box-shadow: 0px 6px 15px 0px grey;
  display: inline-block;
  padding: 5px 5px 0px 5px;
  border-radius: 2%;
}

.paste-demo-gif img{
  width: 100%;
}

h1 {
  font-size: 4.1rem;
  letter-spacing: -0.06em;
  margin-top: 3px;
  margin-left: 19px;
}

h2 {
  margin-top: 31px;
  /* color: #fbca54; */
  /* color: #304276; */
  color: #bd081c;
  /* color: #13097C; */
  /* color: #00b6bd; */
  /* color: #be181d; */
  /* color: #e7594f; */
  /* #44494d */
  font-size: 1.5rem;
  letter-spacing: -0.01em;
  font-weight: 500;
}

.editor {
  position: relative;
  margin: 4vw;
  height: 44vh;
  max-width: 95%;
  -webkit-box-shadow: 0px 6px 15px 0px grey;
          box-shadow: 0px 6px 15px 0px grey;
  text-align: center;
  background-color: #fbfbfb;
  border: var(--border-width) solid #fff;
}

#cursor {
  font-weight: 100;
  opacity: 0;
  -webkit-animation: blinking 1s linear infinite;
          animation: blinking 1s linear infinite;
}

@-webkit-keyframes blinking {
  from,
  49.9% {
    opacity: 0;
  }
  50%,
  to {
    opacity: 0.6;
  }
}

@keyframes blinking {
  from,
  49.9% {
    opacity: 0;
  }
  50%,
  to {
    opacity: 0.6;
  }
}

.checkmark-check {
  stroke: #13097C;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  stroke-dasharray: 88;
  stroke-dashoffset: 88;
  stroke-width: 3;
  -webkit-animation: stroke .8s cubic-bezier(0.650, 0.000, 0.450, 1.000) .1s forwards;
          animation: stroke .8s cubic-bezier(0.650, 0.000, 0.450, 1.000) .1s forwards;
}

@-webkit-keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}



