.social {
  margin: 23px 0px 10px 0px;
  width: 100%;
  text-align: center
}

.tour {
  text-align: center;
  color: #333;
  padding-left: 10%;
  padding-right: 10%;
  font-family: serif;
  margin-bottom: 3px;
}

.tour > h2 {
  margin-top: 50px;
  color: #09143b;
}

.tour p {
  font-size: 0.81rem;
  text-align: justify;
}

.footer-panel {
  background: #09143b;
  width: 100%;
  height: 470px;
  color: white;
  text-align: center;
  padding-top: 50px;
  line-height: 2em;
}

.chevron {
  border-top: 20px solid #b5c6c2;
  border-left: 50vw solid transparent;
  border-right: 48vw solid transparent;
  position: absolute;
}

.subhead {
  color: rgba(255, 255, 255, 0.4);
  text-transform: uppercase;
  margin: 18px 0 1px 0;
}

.feedback-form {
  border-radius: 4px;
  margin-top: 20px;
  margin-bottom: 20px;
  display: inline-block;
  background: lightgrey;
  width: 60vw;
  padding: 0px 30px 30px 20px;
}

.feedback-form button {
  margin-top: 20px;
  width: 20vw;
}



